
.banner {
  display: block;
  max-width: 800px;
  margin: 0 auto;
  height: 100%;
}
.banner .turnplate {
  display: block;
  width: 100%;
  height: auto;
  position: relative;
  margin: 0 auto;
  background-image: url('../../assets/turnplate-bg.png');
  background-size: 100% auto;
  background-repeat: no-repeat;
}
.banner .turnplate img.pointer {
  position: absolute;
  width: 31.5%;
  height: 42.5%;
  left: 34.6%;
  top: 23%;
}
.banner .turnplate canvas.item {
  width: 100%;
}




